#personalStats {
    width: 20rem;
    height: 4rem;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    top: 3rem;
    left: 3rem;
}

h1 {
    margin: 0;
    font-size: 1.5rem;
}

h2 {
    margin: 0;
    font-size: 1rem;
}

#personalStats_lines {
    display: flex;
    align-items: center;
}

#thick_personal_line {
    display: block;
    top: 0;
    left: 0;
    background-color: white;
    height: 0.2rem;
    width: 9rem;
}

#thin_personal_line {
    display: block;
    top: 0;
    left: 0;
    background-color: white;
    height: 0.1rem;
    width: 6.2rem;
}

@media (max-width: 1100px) {
    #personalStats {
        left: 50%;
        transform: translateX(-50%);
        width: 10rem;
        height: 2.5rem;
        top: 2rem;
    }
    h1 {
        font-size: 0.8rem;
    }
    h2, h3, p {
        font-size: 0.7rem;
    }
}